// Panel
html {
  &:not(.bvi-panel) {
    font-size: 100%;

    *,
    ::after,
    ::before {
      box-sizing: border-box;
    }
  }

  body {
    .bvi-panel {
      font-family: Arial, serif !important;
      font-size: 1rem !important;
      padding: .5rem;
      color: black;
      top: 0;
      left: 0;
      right: 0;
      letter-spacing: 0;
      position: relative;
      text-shadow: none;
      line-height: normal;
      border: 0;
      margin-bottom: .3rem;
      background-color: #f7f7f7;
      box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.23);
      z-index: 999999 !important;

      * {
        font-family: Arial, serif !important;
        font-size: 1rem !important;
      }

      a {
        color: black !important;
        outline: 0 !important;
        background-color: transparent !important;
        text-decoration: none !important;

        &:hover,
        &:focus {
          color: black !important;
          outline: 0 !important;
          background-color: transparent !important;
          text-decoration: none !important;
        }

        &.bvi-btn {
          display: inline-block;
          font-weight: 400;
          line-height: 1.5;
          font-size: 0.875rem !important;
          color: #212529 !important;
          text-align: center;
          text-decoration: none;
          vertical-align: middle;
          cursor: pointer;
          -webkit-user-select: none;
          -moz-user-select: none;
          user-select: none;
          background-color: #e0e0e0 !important;
          border: 1px solid #c6c6c6 !important;
          padding: .375rem .75rem;
          border-radius: .25rem;
          position: relative;
          transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;

          &::after {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: 1;
            content: "";
          }

          &:hover,
          &:focus {
            background-color: $btn-hover;
          }

          &.active {
            color: $btn-active-color;
            background-color: $btn-active-bg;
          }

          &.bvi-theme-white {
            background-color: $theme-bg-white;
            color: $theme-color-white;
          }

          &.bvi-theme-black {
            background-color: $theme-bg-black;
            color: $theme-color-black;
          }

          &.bvi-theme-blue {
            background-color: $theme-bg-blue;
            color: $theme-color-blue;
          }

          &.bvi-theme-brown {
            background-color: $theme-bg-brown;
            color: $theme-color-brown;
          }

          &.bvi-theme-green {
            background-color: $theme-bg-green;
            color: $theme-color-green;
          }

          .bvi-images {
            width: 16px;
            height: 16px;
            letter-spacing: normal;
            display: inline-block;
            vertical-align: middle;
            z-index: 1;

            &.bvi-images-cog {
              background-image: url('data:image/png;base64,');
              background-repeat: no-repeat;
            }

            &.bvi-images-eye-slash {
              background-image: url("data:image/png;base64,");
              background-repeat: no-repeat;
            }

            &.bvi-images-arrow {
              background-image: url("data:image/png;base64,");
              background-repeat: no-repeat;
            }

            &.bvi-images-volume-on {
              background-image: url("data:image/png;base64,");
              background-repeat: no-repeat;
            }

            &.bvi-images-volume-off {
              background-image: url("data:image/png;base64,");
              background-repeat: no-repeat;
            }

            &.bvi-images-adjust {
              background-image: url("data:image/png;base64,");
              background-repeat: no-repeat;
            }

            &.bvi-images-off {
              background-image: url("data:image/png;base64,");
              background-repeat: no-repeat;
            }

            &.bvi-images-on {
              background-image: url("data:image/png;base64,");
              background-repeat: no-repeat;
            }

            &.bvi-images-minus-square-o {
              background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQCAMAAAAoLQ9TAAAAHlBMVEUAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAC3KG9qAAAACXRSTlMAODs8dnfx8vPK0lEjAAAAJElEQVR4AWOgD2BkYUUCLIwM7JwogI2BA1WAnYGZFQUw0cehAOJeAaqtH8h4AAAAAElFTkSuQmCC);
              background-repeat: no-repeat;
            }

            &.bvi-images-eye {
              background-image: url(data:image/png;base64,);
              background-repeat: no-repeat;
              width: 32px;
              height: 32px;
            }
          }
        }
      }

      .bvi-block-settings,
      .bvi-block-settings-more {
        margin: 0 auto;

        .bvi-blocks {
          text-align: center;
          display: flex;
          flex-flow: row wrap;

          &.bvi-block-start {
            justify-content: flex-start;
          }

          &.bvi-block-center {
            justify-content: center;
          }

          &.bvi-block-end {
            justify-content: flex-end;
          }
        }

        .bvi-block {
          padding: .688rem .938rem;

          .bvi-block-title {
            text-align: center;
            font-weight: 600 !important;
            font-size: 1rem !important;
            display: block;
            margin-bottom: .8rem;
          }
        }
      }

      .bvi-block-settings {
        max-width: 100%;
      }

      .bvi-modal {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.5);
        z-index: 1050;
        opacity: 0;
        -webkit-transition: opacity 200ms ease-in;
        -moz-transition: opacity 200ms ease-in;
        transition: opacity 200ms ease-in;
        pointer-events: none;
        margin: 0;
        padding: 0;

        &.show {
          opacity: 1;
          pointer-events: auto;
          overflow-y: hidden;
          height: 100%;

          > body {
            overflow-y: hidden !important;
          }
        }

        .bvi-modal-dialog {
          position: relative;
          width: auto;
          max-width: 430px;
          margin: 1.75rem auto;
        }

        .bvi-modal-content {
          position: relative;
          display: -webkit-box;
          display: -webkit-flex;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-orient: vertical;
          -webkit-box-direction: normal;
          -webkit-flex-direction: column;
          -ms-flex-direction: column;
          flex-direction: column;
          background-color: #fff;
          -webkit-background-clip: padding-box;
          background-clip: padding-box;
          border: 1px solid rgba(0, 0, 0, .2);
          border-radius: .3rem;
          outline: 0;
        }

        .bvi-modal-header {
          display: -webkit-box;
          display: -webkit-flex;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-align: center;
          -webkit-align-items: center;
          -ms-flex-align: center;
          align-items: center;
          -webkit-box-pack: justify;
          -webkit-justify-content: space-between;
          -ms-flex-pack: justify;
          justify-content: space-between;
          padding: 15px;
          border-bottom: 1px solid #eceeef;
        }

        .bvi-modal-title {
          margin-top: 0;
          margin-bottom: 0;
          line-height: 1.5;
          font-size: 1.25rem;
          font-weight: 600;
          color: black;
        }

        .bvi-modal-body {
          position: relative;
          -webkit-box-flex: 1;
          -webkit-flex: 1 1 auto;
          -ms-flex: 1 1 auto;
          flex: 1 1 auto;
          padding: 0 1rem 1rem;
          max-height: calc(100vh - 10rem);
          overflow-y: auto;

          &::-webkit-scrollbar {
            width: .8rem;
            background-color: transparent;
            opacity: 0;
          }

          &::-webkit-scrollbar-thumb {
            background-color: #c4c4c4;
            border-radius: .25rem;
          }
        }

        .bvi-modal-close {
          float: right;
          font-family: sans-serif;
          font-size: 1.7rem !important;
          font-weight: 700;
          line-height: 1;
          color: #000;
          text-shadow: 0 1px 0 #fff;
          opacity: .5;
          text-decoration: none;

          &:focus,
          &:hover {
            color: #000;
            text-decoration: none;
            cursor: pointer;
            opacity: .75;
          }
        }
      }
    }
  }
}
